
<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <van-cell-group>
          <van-field label="使用案例如下" readonly />
          <van-field label="地址" value="http://61.163.210.202:9013/" readonly />
          <van-field label="名称" value="gqcg" readonly />
          <van-field label="最终IP端口号" value="http://61.163.210.202:9013/gqcg" readonly />
        </van-cell-group>
        <br>
        <van-cell-group>
          <van-field label="设置baseUrl" readonly />
          <van-field v-model="url1" label="请输入地址：" placeholder="请输入地址" @input="change" />
          <van-field v-model="mc1" label="请输入名称：" placeholder="请输入名称" @input="change" />
          <van-field v-model="dz1" label="最终IP端口号" placeholder="最终IP端口号" readonly />
        </van-cell-group>
        <div class="content">
          <div class="login-button">
            <van-button round type="info" @click="go('VUE_APP_BASE_API')">确认</van-button>
          </div>
        </div>
        <br>
        <van-cell-group>
          <van-field label="设置scUrl" readonly />
          <van-field v-model="url4" label="请输入地址：" placeholder="请输入地址" @input="change" />
          <van-field v-model="mc4" label="请输入名称：" placeholder="请输入名称" @input="change" />
          <van-field v-model="dz4" label="最终IP端口号" placeholder="最终IP端口号" readonly />
        </van-cell-group>
        <div class="content">
          <div class="login-button">
            <van-button round type="info" @click="go('VUE_APP_SC_BASE_API')">确认</van-button>
          </div>
        </div>
        <br>
        <van-cell-group>
          <van-field label="设置fileUrl" readonly />
          <van-field v-model="url2" label="请输入地址：" placeholder="请输入地址" @input="change" />
          <van-field v-model="mc2" label="请输入名称：" placeholder="请输入名称" @input="change" />
          <van-field v-model="dz2" label="最终IP端口号" placeholder="最终IP端口号" readonly />
        </van-cell-group>
        <div class="content">
          <div class="login-button">
            <van-button round type="info" @click="go('VUE_APP_FILE_API')">确认</van-button>
          </div>
        </div>
        <br>
        <van-cell-group>
          <van-field label="设置cfcaUrl" readonly />
          <van-field v-model="url3" label="请输入地址：" placeholder="请输入地址" @input="change" />
          <van-field v-model="mc3" label="请输入名称：" placeholder="请输入名称" @input="change" />
          <van-field v-model="dz3" label="最终IP端口号" placeholder="最终IP端口号" readonly />
        </van-cell-group>
      </div>
      <div class="content">
        <div class="login-button">
          <van-button round type="info" @click="go('VUE_APP_CFCA_API')">确认</van-button>
        </div>
      </div>
      <div class="content">
        <div class="login-button">
          <van-button round type="info" @click="goback">返回登录页</van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

// import { pageRouter } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar.vue'
import { setUrl } from '@/common/js/catch'
export default {
  name: 'WdxmIndex',
  components: {
    headerBar
  },

  data() {
    return {
      dz1: '',
      url1: '',
      mc1: '',
      dz2: '',
      url2: '',
      mc2: '',
      dz3: '',
      url3: '',
      mc3: '',
      dz4: '',
      url4: '',
      mc4: '',
      headerData: {
        title: 'Url',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  created() {
  },
  methods: {
    change() {
      this.dz = this.url + this.mc
    },
    go(key) {
      if (key === 'VUE_APP_BASE_API') {
        this.dz1 = this.url1 + this.mc1
        setUrl(key, this.dz1)
      }
      if (key === 'VUE_APP_SC_BASE_API') {
        this.dz4 = this.url4 + this.mc4
        setUrl(key, this.dz4)
      }
      if (key === 'VUE_APP_FILE_API') {
        this.dz2 = this.url2 + this.mc2
        setUrl(key, this.dz2)
      }
      if (key === 'VUE_APP_CFCA_API') {
        this.dz3 = this.url3 + this.mc3
        setUrl(key, this.dz3)
      }
    },
    goback() {
      this.$router.go(-1)
    }
  }
}
</script>

